import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/HomeView.vue'
import Discover from '../views/DiscoverView.vue'
import Profile from '../views/ProfileView.vue'
import NewRecord from '../views/NewRecordView.vue'
import Record from '../views/RecordView.vue'
import Collections from '../views/CollectionsView.vue'

const routes = [
  {
    path: '/',
    name: 'discover',
    component: Discover
  },
  {
    path: '/home',
    name: 'home',
    component: Home
  },
  {
    path: '/profile',
    name: 'profile',
    component: Profile
  },
  {
    path: '/init',
    name: 'initProfile',
    component: () => import('../views/InitProfileView.vue')
  },
  {
    path: '/register',
    name: 'register',
    component: () => import('../views/RegisterView.vue')
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('../views/LoginView.vue')
  },
  {
    path: '/verify',
    name: 'verify',
    component: () => import('../views/VerifyView.vue')
  },
  {
    path: '/record/new',
    name: "newRecord",
    component: NewRecord,
  },
  {
    path: '/record/:hash',
    name: "record",
    component: Record,
  },
  {
    path: '/collections',
    name: "Collections",
    component: Collections,
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
})

export default router
